import { Component, OnInit } from '@angular/core';
import { BicinsaService } from '../../../../bicinsa.service';
import { Router } from '@angular/router';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { Client } from '../../../../shared/client';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-editperson',
  templateUrl: './editperson.component.html',
  styleUrls: ['./editperson.component.css']
})
export class EditpersonComponent implements OnInit {

  constructor(private bicinsaService: BicinsaService, private router: Router, private formBuilder: FormBuilder) { }

  submitted;
  preloadingstate: boolean = true;
  formulario: FormGroup;
  cliente: Client = new Client();
  usersesion;

  ngOnInit() {
    this.submitted = false;
    this.usersesion = JSON.parse(localStorage.getItem("user"));
    this.bicinsaService.getInfoUser(this.usersesion.ID)
      .subscribe((data) => {
        this.cliente = data;
        this.preloadingstate = false;
      });

      this.formulario = this.formBuilder.group({
        name: ['', [Validators.required]],
        lastname: ['', [Validators.required]],
        phone: ['', [Validators.required]],
        email: ['', [Validators.required]],
        passw: ['', [Validators.required]],
      });
  }

  get f() { return this.formulario.controls; }

  updateInfo() {
    try {
      this.submitted = true;
      if (this.formulario.invalid) {
        return;
      }
      this.preloadingstate = true;
      let formData = new FormData();
      this.cliente.document = this.usersesion.document;
      formData.append('client', JSON.stringify(this.cliente));
      this.bicinsaService.updateInfoUser(formData)
        .subscribe((data) => {
          this.alertConfirm();
          this.preloadingstate = false;
          this.cliente = new Client();
          this.ngOnInit();
        },
          err => console.log(err),
        );
    }
    catch (e) {
      console.log(e);
    }
  }


  alertConfirm() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });

    Toast.fire({
      type: 'success',
      title: 'Datos personales actualizados con Ã©xito.'
    })
  }

}
