import { Component, OnInit } from '@angular/core';
import { MapsAPILoader } from '@agm/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
declare var google;

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {

  formulario: FormGroup;
  lat = -8.089709;
  lng = -79.089663;
  zoom = 11;
  markers = [];
  filteredMarkers = [];
  addressList = [{ name: 'Direccion 1' }, { name: 'Direccion 2' }, { name: 'Direccion 3' }];
  constructor(
    private mapsAPILoader: MapsAPILoader, private formBuilder: FormBuilder
  ) { }

  getLocations(): Array<{ latitude: number, longitude: number, name: string }> {
    return [
      { 'latitude': -8.138934, 'longitude': -79.054185, 'name': 'PLANTA QUÃMICOS Y OFICINAS' },
      { 'latitude': -8.089709, 'longitude': -79.089663, 'name': 'PLANTA ALIMENTARIA' }
    ];
  }
  ngOnInit() {
    this.tipodocumentoaenviar = 'DNI';
    this.markers = this.getLocations();
    this.mapsAPILoader.load().then(() => {
      const center = new google.maps.LatLng(this.lat, this.lng);
      this.filteredMarkers = this.markers.filter(m => {
        const markerLoc = new google.maps.LatLng(m.latitude, m.longitude, m.name);
        const distanceInKm = google.maps.geometry.spherical.computeDistanceBetween(markerLoc, center) / 1000;
        if (distanceInKm < 50.0) {
          return m;
        }
      });
    });
  }

  tipodocumentoaenviar;
  nameinput;
  showform(tipodocumento) {
    this.tipodocumentoaenviar = tipodocumento;
  }

}
