import { Component, OnInit, HostListener } from '@angular/core';
import { Observable } from 'rxjs';
import { Router, NavigationExtras } from '@angular/router';
import { BicinsaService } from '../bicinsa.service';
import { Product } from '../shared/product';
import { of } from 'rxjs/observable/of';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
  host: {
    '(document:click)': 'onClick($event)',
  },
})
export class HeaderComponent implements OnInit {

  constructor(private bicinsaService: BicinsaService, private router: Router) { }

  showHide: boolean = false;
  showProfile: boolean = false;
  public shoppingCartItems$: Observable<Product[]> = of([]);
  public shoppingCartItems: Product[] = [];
  showHideLoginRegister: boolean = false;
  showlistproducts: boolean = false;
  usersesion;
  showmessage: boolean;
  preloadingstate: boolean;

  ngOnInit() {
    this.chargeItems();
    this.bicinsaService.currentUserName$
      .subscribe(
        _ =>
          this.getSession()
      );
    this.bicinsaService.currentShowStore$.map(res => { this.showmessage = res }).toPromise();
  }

  chargeItems() {
    this.shoppingCartItems$ = this.bicinsaService.getItems();
    this.shoppingCartItems$.subscribe(_ => this.shoppingCartItems = _);
    if (this.shoppingCartItems.length > 0) {
      this.showlistproducts = true;
    } else {
      this.showlistproducts = false;
    }
  }

  getSession() {
    this.usersesion = JSON.parse(localStorage.getItem("user"));
    if (this.usersesion != null) {
      this.showHideLoginRegister = false;
    } else {
      this.showHideLoginRegister = true;
    }
  }

  showListProducts() {
    this.showHide = !this.showHide;
    this.chargeItems();
  }

  @HostListener('document:click', ['$event']) clickedOutside($event) {
    if (this.showHide) {
      this.showHide = false;
    }
    if (this.showProfile) {
      this.showProfile = false;
    } if (this.navbarOpen) {
      this.navbarOpen = false;
    }
  }
  removeItem(item: Product) {
    if (this.usersesion != null) {
      this.preloadingstate = true;
      try {
        let formData = new FormData();
        formData.append('idclient', this.usersesion.ID);
        formData.append('productid', item.ID);
        formData.append('idpresentation', item.presentation.ID);
        this.bicinsaService.deleteFromOrder(formData)
          .subscribe((data) => {
            this.preloadingstate = false;
            this.bicinsaService.getItems();
            if (this.router.url == '/cart') {
              this.router.navigateByUrl('/', { skipLocationChange: true }).then(() =>
                this.router.navigate(["/cart"]));
            }
          },
            err => console.log(err),
          );
      } catch (e) {
        console.log(e);
      }
    }
    else {
      this.bicinsaService.removeFromCart(item);
      this.chargeItems();
      if (this.router.url == '/cart') {
        this.router.navigateByUrl('/', { skipLocationChange: true }).then(() =>
          this.router.navigate(["/cart"]));
      }
    }
  }

  searchproducto: string = '';
  search() {
    let navigationExtras: NavigationExtras = {
      queryParams: { 'q': this.searchproducto }
    };
    this.router.navigate(['/search'], navigationExtras);
    this.searchproducto = '';
  }

  logOut() {
    localStorage.clear();
    let router = this.router.url;
    this.router.navigateByUrl(router, { skipLocationChange: true }).then(() =>
      this.router.navigate(["/"]));
    this.ngOnInit();
    this.showProfile = false;
  }

  navbarOpen = false;
  toggleNavbar() {
    this.navbarOpen = !this.navbarOpen;
  }

  clickedInside($event: Event) {
    $event.preventDefault();
    $event.stopPropagation();
  }

  quantity: any = 1;
  stockadd(idproduct, idpresentation, isChecked: boolean) {
    let productselected = this.shoppingCartItems.filter(_ => _.ID == idproduct);
    let dataTemp = productselected.filter(_ => _.presentation.ID == idpresentation);
    if (this.usersesion != null) {
      let formData = new FormData();
      formData.append('idclient', this.usersesion.ID);
      formData.append('productid[0]', idproduct);
      formData.append('presentationid[0]', idpresentation);
      if (isChecked) {
        if ((parseFloat((dataTemp[0].quantity).toString())) < (parseFloat((dataTemp[0].stock).toString()))) {
          if (dataTemp[0].quantity.length > 3) {
            dataTemp[0].quantity = dataTemp[0].quantity.replace(',', '');
          }
          let newquantity = parseInt(dataTemp[0].quantity) + 1;
          var dataquantity = newquantity.toString().replace(/\,/g, '');
          dataquantity = dataquantity.toString().split('').reverse().join('').replace(/(?=\d*\.?)(\d{3})/g, '$1,');
          dataquantity = dataquantity.split('').reverse().join('').replace(/^[\,]/, '');
          formData.append("quantity[0]", dataquantity);
        } else {
          return this.alertNostock();
        }
      }
      else {
        if (dataTemp[0].quantity == '1') {
          return;
        } else {
          if (dataTemp[0].quantity.length > 3) {
            dataTemp[0].quantity = dataTemp[0].quantity.replace(',', '');
          }
          let newquantity = parseInt(dataTemp[0].quantity) - 1;
          var dataquantity = newquantity.toString().replace(/\,/g, '');
          dataquantity = dataquantity.toString().split('').reverse().join('').replace(/(?=\d*\.?)(\d{3})/g, '$1,');
          dataquantity = dataquantity.split('').reverse().join('').replace(/^[\,]/, '');
          formData.append("quantity[0]", dataquantity);
        }
      }
      this.bicinsaService.saveOrder(formData)
        .subscribe((data) => {
          this.alertConfirm();
          this.ngOnInit();
        },
          err => console.log(err),
        );
    }
    else {
      if (isChecked) {
        if ((dataTemp[0].quantity).toString() != dataTemp[0].stock) {
          dataTemp[0].quantity = '1';
          this.bicinsaService.addToCart(dataTemp[0]);
        }
      } else {
        if (dataTemp[0].quantity == '1') {
          dataTemp[0].quantity = '1';
        } else {
          dataTemp[0].quantity = '-1';
          this.bicinsaService.addToCart(dataTemp[0]);
        }
      }
      this.ngOnInit();
    }
  }


  alertNostock() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });

    Toast.fire({
      type: 'warning',
      title: 'Ya tienes todo el stock agregado'
    })
  }
  alertConfirm() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });

    Toast.fire({
      type: 'success',
      title: 'Lista de pedidos actualizado'
    })
  }
}
