import { Component, OnInit } from '@angular/core';
import { Client } from '../../shared/client';
import { Router, NavigationExtras } from '@angular/router';
import { BicinsaService } from '../../bicinsa.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {

  constructor(private bicinsaService: BicinsaService, private router: Router, private formBuilder: FormBuilder) { }

  cliente = new Client();
  preloadingstate: boolean;
  submitted;
  formulario: FormGroup;
  usuarioincorrecto: boolean;

  ngOnInit() {
    this.submitted = false;
    this.formulario = this.formBuilder.group({
      email: ['', [Validators.required]]
    });
  }
  get f() { return this.formulario.controls; }

  forgotpass() {
    this.submitted = true;

    if (this.formulario.invalid) {
      return;
    }

    let formData = new FormData();
    formData.append('correo', this.cliente.email);

    this.preloadingstate = true;
    this.bicinsaService.forgotPassword(formData)
      .subscribe((data) => {
        if (data.type == 0) {
          this.usuarioincorrecto = true;
          this.preloadingstate = false;
        } else {
          this.preloadingstate = false;
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timer: 3000
          });

          Toast.fire({
            type: 'success',
            title: 'Se envio un email para reestablecer la contraseÃ±a.'
          })
          this.router.navigate(["/"]);
        }
      });
  }

}
