import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { BicinsaService } from '../../../bicinsa.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-category',
  templateUrl: './category.component.html',
  styleUrls: ['./category.component.css']
})
export class CategoryComponent implements OnInit {


  constructor(public bicinsaService: BicinsaService, private route: ActivatedRoute, private router: Router) { }
  p = 0;
  dataCategory;
  dataTempProduct = [];
  userSubscription: Subscription;
  ngOnInit() {
    this.userSubscription = this.route.params.subscribe(
      (params: Params) => {
        this.bicinsaService.getListProduct(params['idcat'])
          .subscribe((data) => {
            console.log(params['idcat'])
            this.dataCategory = data;
          });
      }
    );
  }
  onPageChange(page: number) {
    this.p = page;
    window.scrollTo(0, 0);
  }
}
