import { Component, OnInit, HostListener } from '@angular/core';
import { Client } from '../../../shared/client';
import { Router, NavigationExtras } from '@angular/router';
import { BicinsaService } from '../../../bicinsa.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { HeaderComponent } from '../../../header/header.component';

@Component({
  providers: [HeaderComponent],
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  submitted;
  formulario: FormGroup;
  constructor(private bicinsaService: BicinsaService, private router: Router, private formBuilder: FormBuilder, private header: HeaderComponent) { }

  cliente = new Client();
  preloadingstate: boolean;
  ngOnInit() {
    this.submitted = false;
    this.formulario = this.formBuilder.group({
      email: ['', [Validators.required]],
      passw: ['', [Validators.required]]
    });
  }
  get f() { return this.formulario.controls; }

  visible: boolean = false;
  login() {
    this.submitted = true;

    if (this.formulario.invalid) {
      return;
    }

    let formData = new FormData();
    formData.append('user', this.cliente.user);
    formData.append('passw', this.cliente.passw);

    this.preloadingstate = true;
    this.bicinsaService.loginCliente(formData)
      .subscribe((data) => {
        if (data.type != 0) {
          //ALMACENAS USUARIO EN LOCALSTORAGE
          localStorage.setItem('user', JSON.stringify(data));
          //LISTAS PRODUCTOS QUE HAS AGREGADO ANTES DE AUTENTICARTE
          var localstoragedata = JSON.parse(localStorage.getItem("shopping-list"));
          //LISTAS LAS ORDENES PENDIENTES POR USUARIO
          this.bicinsaService.getOrderUserLogin(data.ID)
            .subscribe((data) => {
              localStorage.removeItem('totals');
              //SI TIENES ORDENES PENDIENTES Y TIENES PRODUCTOS EN EL LOCALSTORAGE
              if (data.length > 0 && localstoragedata != null) {
                for (let i = 0; i < data.length; i++) {
                  this.bicinsaService.addToCart(data[i]);
                }
                this.bicinsaService.verificarSession();
                localStorage.removeItem('shopping-list');
              }
              //SI NO TIENES ORDENES PENDIENTES Y TIENES PRODUCTOS EN EL LOCALSTORAGE
              else if (data.length == 0 && localstoragedata != null) {
                this.bicinsaService.verificarSession();
              } else {
                this.header.chargeItems();
              }
            });
          this.preloadingstate = false;
          this.bicinsaService.setCurrentUserName(data);
          this.router.navigate(["/"]);
        } else {
          this.visible = true;
          this.preloadingstate = false;
        }
      });
  }
}
