import { Component, OnInit } from '@angular/core';
import { BicinsaService } from '../../../bicinsa.service';
import { AccountComponent } from '../account.component';

@Component({
  selector: 'app-orders',
  templateUrl: './orders.component.html',
  styleUrls: ['./orders.component.css']
})
export class OrdersComponent implements OnInit {

  constructor(private bicinsaService: BicinsaService, private globals: AccountComponent) { }

  showOrder: boolean;
  dataTempOrder;
  preloadingstate: boolean = true;
  ngOnInit() {
    this.bicinsaService.getOrders(this.globals.usuarioGlobal.ID)
      .subscribe((data) => {
        if (data.length > 0) {
          this.dataTempOrder = data;
          this.showOrder = true;
          this.preloadingstate = false;
        } else {
          this.showOrder = false;
          this.preloadingstate = false;
        }
      });
  }

}
