import { Component, OnInit } from '@angular/core';
import { BicinsaService } from '../../bicinsa.service';
import { Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { Product } from '../../shared/product';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {

  public shoppingCartItems$: Observable<Product[]> = of([]);
  public shoppingCartItems: Product[] = [];

  constructor(private bicinsaService: BicinsaService, private router: Router) { }

  usersesion;
  showCart;
  preloadingstate: boolean;
  ngOnInit() {
    this.usersesion = JSON.parse(localStorage.getItem("user"));

    this.shoppingCartItems$ = this
      .bicinsaService
      .getItems();
    this.shoppingCartItems$.subscribe(_ => this.shoppingCartItems = _);
    if (this.shoppingCartItems.length > 0) {
      this.showCart = true;
    } else {

      this.router.navigateByUrl('/checkout', { skipLocationChange: true }).then(() =>
        this.router.navigate(["/"]));
    }

    if (this.usersesion != null) {
      this.bicinsaService.currentShowStore$.map(res => { this.showCart = res }).toPromise();
    }
    if (this.usersesion == null) {
      this.router.navigateByUrl('/checkout', { skipLocationChange: true }).then(() =>
        this.router.navigate(["/"]));
    }
  }

  updateOrder() {
    this.preloadingstate = true;
    let formData = new FormData();
    formData.append('idclient', this.usersesion.ID);
    this.bicinsaService.updateOrder(formData)
      .subscribe((data) => {
        this.preloadingstate = false;
        const Toast = Swal.mixin({
          toast: true,
          position: 'top',
          showConfirmButton: false,
          timer: 3000
        });

        Toast.fire({
          type: 'success',
          title: 'Pedido realizado con Ã©xito.'
        })
        this.router.navigate(["/"]);
      });
  }

}
