import { Component, OnInit, HostListener } from '@angular/core';
import { BicinsaService } from '../../bicinsa.service';
import { Router } from '@angular/router';
import { Observable, of } from 'rxjs';
import { Product } from '../../shared/product';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {

  public shoppingCartItems$: Observable<Product[]> = of([]);
  public shoppingCartItems: Product[] = [];
  preloadingstate: boolean = true;

  constructor(private bicinsaService: BicinsaService, private router: Router) {
    this.onResize();
  }

  @HostListener('window:resize', ['$event'])
  onResize(event?) {
    this.innerWidth = window.innerWidth;
    if (this.innerWidth <= 768) { // 768px portrait
      this.showMobile = true;
    } else {
      this.showMobile = false;
    }
  }

  usersesion;
  showsesiontext: boolean = false;
  showCart; showMobile: boolean;
  innerWidth;
  ngOnInit() {
    this.usersesion = JSON.parse(localStorage.getItem("user"));
    if (this.usersesion != null) {
      this.showsesiontext = true;
    }

    this.shoppingCartItems$ = this
      .bicinsaService
      .getItems();
    this.shoppingCartItems$.subscribe(_ => this.shoppingCartItems = _);
    if (this.shoppingCartItems.length > 0) {
      this.showCart = true;
    }

    if (this.usersesion != null) {
      this.bicinsaService.currentShowStore$.map(res => { this.showCart = res }).toPromise();
    }
    this.preloadingstate = false;
  }

  quantity: any = 1;
  stockadd(idproduct, idpresentation, isChecked: boolean) {
    let productselected = this.shoppingCartItems.filter(_ => _.ID == idproduct);
    let dataTemp = productselected.filter(_ => _.presentation.ID == idpresentation);
    if (this.usersesion != null) {
      let formData = new FormData();
      formData.append('idclient', this.usersesion.ID);
      formData.append('productid[0]', idproduct);
      formData.append('presentationid[0]', idpresentation);
      if (isChecked) {
        if ((parseFloat((dataTemp[0].quantity).toString())) < (parseFloat((dataTemp[0].stock).toString()))) {
          if (dataTemp[0].quantity.length > 3) {
            dataTemp[0].quantity = dataTemp[0].quantity.replace(',', '');
          }
          let newquantity = parseInt(dataTemp[0].quantity) + 1;
          var dataquantity = newquantity.toString().replace(/\,/g, '');
          dataquantity = dataquantity.toString().split('').reverse().join('').replace(/(?=\d*\.?)(\d{3})/g, '$1,');
          dataquantity = dataquantity.split('').reverse().join('').replace(/^[\,]/, '');
          formData.append("quantity[0]", dataquantity);
        } else {
          return this.alertNostock();
        }
      }
      else {
        if (dataTemp[0].quantity == '1') {
          return;
        } else {
          if (dataTemp[0].quantity.length > 3) {
            dataTemp[0].quantity = dataTemp[0].quantity.replace(',', '');
          }
          let newquantity = parseInt(dataTemp[0].quantity) - 1;
          var dataquantity = newquantity.toString().replace(/\,/g, '');
          dataquantity = dataquantity.toString().split('').reverse().join('').replace(/(?=\d*\.?)(\d{3})/g, '$1,');
          dataquantity = dataquantity.split('').reverse().join('').replace(/^[\,]/, '');
          formData.append("quantity[0]", dataquantity);
        }
      }
      this.bicinsaService.saveOrder(formData)
        .subscribe((data) => {
          this.alertConfirm();
          this.ngOnInit();
        },
          err => console.log(err),
        );
    }
    else {
      if (isChecked) {
        if ((dataTemp[0].quantity).toString() != dataTemp[0].stock) {
          dataTemp[0].quantity = '1';
          this.bicinsaService.addToCart(dataTemp[0]);
        }
      } else {
        if (dataTemp[0].quantity == '1') {
          dataTemp[0].quantity = '1';
        } else {
          dataTemp[0].quantity = '-1';
          this.bicinsaService.addToCart(dataTemp[0]);
        }
      }
      this.ngOnInit();
    }
  }

  removeItem(item: Product, index) {
    if (this.usersesion != null) {
      try {
        this.preloadingstate = true;
        let formData = new FormData();
        formData.append('idclient', this.usersesion.ID);
        formData.append('productid', item.ID);
        formData.append('presentationid', item.presentation.ID);
        this.bicinsaService.deleteFromOrder(formData)
          .subscribe((data) => {
            this.preloadingstate = false;
            this.alertConfirm();
            this.bicinsaService.getItems();
          },
            err => console.log(err),
          );
      } catch (e) {
        console.log(e);
      }
    }
    else {
      this.bicinsaService.removeFromCart(index);
      if (this.router.url == '/cart') {
        this.router.navigateByUrl('/', { skipLocationChange: true }).then(() =>
          this.router.navigate(["/cart"]));
      }
    }
  }

  nextCheckout() {
    if (this.usersesion != null) {
      this.router.navigate(["/checkout"]);
    } else {
      this.router.navigate(["/authenticate/login"]);
    }
  }
  alertNostock() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });

    Toast.fire({
      type: 'warning',
      title: 'Ya tienes todo el stock agregado'
    })
  }
  alertConfirm() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });

    Toast.fire({
      type: 'success',
      title: 'Lista de pedidos actualizado'
    })
  }

}
