import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Router, NavigationStart } from '@angular/router';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { BicinsaService } from '../../bicinsa.service';

@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css'],
  animations: [
    trigger('slide', [
      state('up', style({ height: 0 })),
      state('down', style({ height: '*' })),
      transition('up <=> down', animate(200))
    ])
  ]
})
export class ProductComponent implements OnInit {

  p = 0;
  preloadingstate: boolean = true;
  showProductGeneral: boolean;
  constructor(private bicinsaService: BicinsaService, private router: Router) {
    this.router.events.subscribe((e) => {
      if (e instanceof NavigationStart) {
        if (e.url === '/productos') {
          this.showProductGeneral = true;
        } else {
          this.showProductGeneral = false;
        }
      }
    })
  }

  dataProductGeneral;
  dataCategory;
  ngOnInit() {
    if (this.router.url == '/productos') {
      this.showProductGeneral = true;
    } else {
      this.showProductGeneral = false;
    }

    this.bicinsaService.listCategory()
      .subscribe((data) => {
        this.dataCategory = data;
        for (let i = 0; i < this.dataCategory.length; i++) {
          this.dataCategory[i].active = false;
          if (this.dataCategory[i].subcategorias.length > 0) {
            this.dataCategory[i].type = 'dropdown';
          } else {
            this.dataCategory[i].type = 'simple';
          }
        }
      });

    this.bicinsaService.listProductGeneral()
      .subscribe((data) => {
        this.dataProductGeneral = data;
        this.preloadingstate = false;
      });
  }

  toggle(currentMenu) {
    if (currentMenu.type === 'dropdown') {
      this.dataCategory.forEach(element => {
        if (element === currentMenu) {
          currentMenu.active = !currentMenu.active;
        } else {
          element.active = false;
        }
      });
    }
  }

  getState(currentMenu) {

    if (currentMenu.active) {
      return 'down';
    } else {
      return 'up';
    }
  }
  onPageChange(page: number) {
    this.p = page;
    window.scrollTo(0, 0);
  }
}
