import { Component, OnInit } from '@angular/core';
import { Router, NavigationExtras, ActivatedRoute, Params } from '@angular/router';
import { BicinsaService } from '../../bicinsa.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {

  constructor(private bicinsaService: BicinsaService, private route: ActivatedRoute, private router: Router, private formBuilder: FormBuilder) { }

  preloadingstate: boolean;
  submitted;
  formulario: FormGroup;
  usuarioincorrecto: boolean;
  userSubscription: Subscription;
  pass;
  repass;
  dni;

  ngOnInit() {
    this.submitted = false;
    this.formulario = this.formBuilder.group({
      pass: ['', [Validators.required]],
      repass: ['', [Validators.required]]
    });

    this.userSubscription = this.route.queryParams.subscribe(
      (params: Params) => {
        this.dni = params['u'];
        let formData = new FormData();
        formData.append('token', params['token']);
        formData.append('iduser', params['u']);

        this.bicinsaService.confirmTokenUser(formData)
          .subscribe((data) => {
            if (data.type == 0) {
              this.router.navigate(["/recuperar-contraseÃ±a"]);
            }
          });
      }
    );
  }

  get f() { return this.formulario.controls; }

  resetpass() {
    try {
      this.submitted = true;
      if (this.formulario.invalid) {
        return;
      }

      this.preloadingstate = false;
      if (this.pass == this.repass) {
        let formData = new FormData();
        formData.append('pass', this.pass);
        formData.append('dni', this.dni);
        this.preloadingstate = true;

        this.bicinsaService.resetPassword(formData)
          .subscribe((data) => {
            const Toast = Swal.mixin({
              toast: true,
              position: 'top',
              showConfirmButton: false,
              timer: 3000
            });

            Toast.fire({
              type: 'success',
              title: 'ContraseÃ±a actualizada con Ã©xito.'
            })
            this.router.navigate(["/authenticate/login"]);
          });
      } else {
        this.preloadingstate = false;
        this.usuarioincorrecto = true;
      }
    }
    catch (e) {
      console.log(e)
    }
  }


}
