import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { BicinsaService } from '../../bicinsa.service';
import { Subscription } from 'rxjs/Subscription';
import { trigger, state, style, transition, animate } from '@angular/animations';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
  animations: [
    trigger('slide', [
      state('up', style({ height: 0 })),
      state('down', style({ height: '*' })),
      transition('up <=> down', animate(200))
    ])
  ]
})
export class SearchComponent implements OnInit {

  constructor(public bicinsaService: BicinsaService, private route: ActivatedRoute, private router: Router) { }
  itemsearch = "";
  dataCategory;
  dataTempProduct;
  p;
  userSubscription: Subscription;
  ngOnInit() {

    this.bicinsaService.listCategory()
      .subscribe((data) => {
        this.dataCategory = data;
        for (let i = 0; i < this.dataCategory.length; i++) {
          this.dataCategory[i].active = false;
          if (this.dataCategory[i].subcategorias.length > 0) {
            this.dataCategory[i].type = 'dropdown';
          } else {
            this.dataCategory[i].type = 'simple';
          }
        }
      });

    this.userSubscription = this.route.queryParams.subscribe(
      (params: Params) => {
        this.itemsearch = (params['q']);
        this.bicinsaService.getSearchProduct(params['q'])
          .subscribe((data) => {
            this.dataTempProduct = data;
          });
      }
    );

  }
  toggle(currentMenu) {
    if (currentMenu.type === 'dropdown') {
      this.dataCategory.forEach(element => {
        if (element === currentMenu) {
          currentMenu.active = !currentMenu.active;
        } else {
          element.active = false;
        }
      });
    }
  }

  getState(currentMenu) {

    if (currentMenu.active) {
      return 'down';
    } else {
      return 'up';
    }
  }
}
