import { Component, OnInit } from '@angular/core';
import { Contact } from '../../../shared/contact';
import { BicinsaService } from '../../../bicinsa.service';
import { Router, NavigationExtras } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2'

@Component({
  selector: 'app-contactperson',
  templateUrl: './contactperson.component.html',
  styleUrls: ['./contactperson.component.css']
})
export class ContactpersonComponent implements OnInit {

  constructor(private bicinsaService: BicinsaService, private router: Router, private formBuilder: FormBuilder) { }
  submitted;
  contact = new Contact();
  formulario: FormGroup;
  token = "";
  preloadingstate: boolean;
  ngOnInit() {
    this.submitted = false;
    this.formulario = this.formBuilder.group({
      nombre: ['', [Validators.required]],
      phone: ['', [Validators.required]],
      email: ['', [Validators.required]],
      dni: ['', [Validators.required, Validators.minLength(8), Validators.pattern(/^-?[\d.]+(?:e-?\d+)?$/)]],
      consulta: ['', [Validators.required]]
    });
  }

  get f() { return this.formulario.controls; }

  arraycliente;
  sendContact() {
    this.submitted = true;
    if (this.formulario.invalid) {
      return;
    }
    this.preloadingstate = true;
    let formData = new FormData();
    formData.append('contact', JSON.stringify(this.contact));
    this.bicinsaService.sendContact(formData)
      .subscribe((data) => {
        this.preloadingstate = false;
        this.alertConfirm();
      });
  }

  alertConfirm() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });
    Toast.fire({
      type: 'success',
      title: 'Su mensaje fue enviado.'
    })
    this.ngOnInit();
  }


}
