import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Client } from '../../../../shared/client';
import { BicinsaService } from '../../../../bicinsa.service';
import { Router, NavigationExtras } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2'

@Component({
  selector: 'app-registerperson',
  templateUrl: './registerperson.component.html',
  styleUrls: ['./registerperson.component.css']
})
export class RegisterpersonComponent implements OnInit {


  constructor(private bicinsaService: BicinsaService, private router: Router, private formBuilder: FormBuilder) { }

  submitted;
  formulario: FormGroup;
  token = "";
  preloadingstate: boolean;
  cliente = new Client();
  ngOnInit() {
    this.submitted = false;
    this.formulario = this.formBuilder.group({
      nombre: ['', [Validators.required]],
      apellidos: ['', [Validators.required]],
      phone: ['', [Validators.required]],
      email: ['', [Validators.required]],
      dni: ['', [Validators.required, Validators.minLength(8), Validators.pattern(/^-?[\d.]+(?:e-?\d+)?$/)]],
      passw: ['', [Validators.required]]
    });
  }

  get f() { return this.formulario.controls; }

  arraycliente;
  saveCliente() {
    this.submitted = true;
    if (this.formulario.invalid) {
      return;
    }
    this.preloadingstate = true;
    this.cliente.type = 'person';
    let formData = new FormData();
    formData.append('cliente', JSON.stringify(this.cliente));
    this.bicinsaService.saveCliente(formData)
      .subscribe((data) => {
        this.arraycliente = data;
        if (this.arraycliente.type == 2) {
          this.preloadingstate = false;
          this.alertduplicateDNI();
        } else {
          this.preloadingstate = false;
          this.alertConfirm();
        }
      });
  }

  alertConfirm() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });
    Toast.fire({
      type: 'success',
      title: 'Revise su correo electrÃ³nico para verificar su cuenta.'
    })
    this.ngOnInit();
  }

  alertduplicateDNI() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });
    Toast.fire({
      type: 'warning',
      title: 'DNI ya estÃ¡ registrado'
    })
  }
}
