import { Component, OnInit } from '@angular/core';
import { BicinsaService } from '../../../bicinsa.service';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-subcategory',
  templateUrl: './subcategory.component.html',
  styleUrls: ['./subcategory.component.css']
})
export class SubcategoryComponent implements OnInit {

  constructor(public bicinsaService: BicinsaService, private route: ActivatedRoute, private router: Router) { }
  p = 0;
  dataSubCategory;
  userSubscription: Subscription;
  ngOnInit() {
    this.userSubscription = this.route.params.subscribe(
      (params: Params) => {
        this.bicinsaService.getListSubProduct(params['idcat'], params['idsub'])
          .subscribe((data) => {
            this.dataSubCategory = data;
          });
      }
    );
  }

  onPageChange(page: number) {
    this.p = page;
    window.scrollTo(0, 0);
  }

}
