import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { BicinsaService } from '../../../bicinsa.service';
import { Subscription } from 'rxjs/Subscription';
import { Product } from '../../../shared/product';
import { Presentation } from '../../../shared/presentation';
import { Observable, of } from 'rxjs';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-detail-product',
  templateUrl: './detail-product.component.html',
  styleUrls: ['./detail-product.component.css']
})
export class DetailProductComponent implements OnInit {

  constructor(public bicinsaService: BicinsaService, private route: ActivatedRoute, private router: Router) { }

  dataDetailProduct;
  preloadingstate: boolean = true;
  userSubscription: Subscription;
  public shoppingCartItems$: Observable<Product[]> = of([]);
  public shoppingCartItems: Product[] = [];
  ficha;
  seguridad;
  usuario;
  submitted = false;
  usersesion;

  ngOnInit() {
    this.usersesion = JSON.parse(localStorage.getItem("user"));

    this.userSubscription = this.route.params.subscribe(
      (params: Params) => {
        this.bicinsaService.getDetailProduct(params['id'])
          .subscribe((data) => {
            this.dataDetailProduct = data;
            this.preloadingstate = false;
          });
      }
    );

    this.shoppingCartItems$ = this
      .bicinsaService
      .getItems();
    this.shoppingCartItems$.subscribe(_ => this.shoppingCartItems = _);
  }

  presentation;
  presentationID;
  savePresentation(idpresentation) {
    this.presentation = new Presentation();
    this.presentation = this.dataDetailProduct[0].presentaciones.filter(x => x.ID == idpresentation);
    this.presentationID = this.presentation[0].ID;
  }

  format() {
    if (this.quantity > 0) {
      var num = this.quantity.toString().replace(/\,/g, '');
      num = num.toString().split('').reverse().join('').replace(/(?=\d*\.?)(\d{3})/g, '$1,');
      num = num.split('').reverse().join('').replace(/^[\,]/, '');
      this.quantity = num;
    } else {
      this.quantity = 1;
    }
  }

  quitformat() {
    var long = this.quantity;
    if (long.length > 3) {
      var num = this.quantity.replace(',', '');
      this.quantity = num;
    }
  }

  quantity: any = 1;
  stockadd(isChecked: boolean) {
    this.quitformat();
    if (this.quantity > 0) {
      if (isChecked) {
        if (parseFloat((this.quantity).toString()) < parseInt(this.dataDetailProduct[0].stock)) {
          this.quantity = (parseFloat((this.quantity).toString()) + 1);
        }
      } else {
        if (this.quantity == 1) {
          this.quantity = 1;
        } else {
          this.quantity = (parseFloat((this.quantity).toString()) - 1);
        }
      }
      this.format();
    }
    else {
      this.alertnumbervalid();
    }
  }

  product;
  addToCart() {
    if (this.presentation != undefined) {
      if (this.usersesion != null) {
        console.log(this.shoppingCartItems);
        let productselected = this.shoppingCartItems.filter(x => x.ID == this.dataDetailProduct[0].ID);
        let dataTemp = productselected.filter(_ => _.presentation.ID == this.presentation[0].ID);
        try {
          let formData = new FormData();
          formData.append('idclient', this.usersesion.ID);
          formData.append('productid[0]', this.dataDetailProduct[0].ID);
          formData.append('presentationid[0]', this.presentation[0].ID);

          if (dataTemp.length > 0) {
            if ((parseFloat((dataTemp[0].quantity).toString())) < (parseFloat((dataTemp[0].stock).toString()))) {
              if (dataTemp[0].quantity.length > 3) {
                dataTemp[0].quantity = dataTemp[0].quantity.replace(',', '');
              }
              if (this.quantity.length > 3) {
                var num = this.quantity.replace(',', '');
                this.quantity = num;
              }
              let newquantity = parseInt(this.quantity) + parseInt(dataTemp[0].quantity);
              var dataquantity = newquantity.toString().replace(/\,/g, '');
              dataquantity = dataquantity.toString().split('').reverse().join('').replace(/(?=\d*\.?)(\d{3})/g, '$1,');
              dataquantity = dataquantity.split('').reverse().join('').replace(/^[\,]/, '');
              formData.append("quantity[0]", dataquantity);
            } else {
              return this.alertNostock();
            }
          } else {
            formData.append("quantity[0]", this.quantity.toString());
          }
          this.bicinsaService.saveOrder(formData)
            .subscribe((data) => {
              this.alertConfirm();
              this.bicinsaService.getItems();
              this.ngOnInit();
              this.quantity = 1;
            },
              err => console.log(err),
            );
        } catch (e) {
          console.log(e);
        }
      }
      else {
        this.product = new Product();
        this.product.ID = this.dataDetailProduct[0].ID;
        this.product.name = this.dataDetailProduct[0].name;
        this.product.image = this.dataDetailProduct[0].image;
        this.product.quantity = this.quantity;
        this.product.stock = this.dataDetailProduct[0].stock;
        this.product.presentation = this.presentation[0];
        this.bicinsaService.addToCart(this.product);
        this.alertConfirm();
        this.ngOnInit();
        this.quantity = 1;
      }
    } else {
      this.alertNoPresentation();
    }
  }

  sendmail(tipo, productname, category, image) {
    if (this.usersesion != null) {
      this.preloadingstate = true;
      let formData = new FormData();
      if (tipo == 'ficha') {
        formData.append('productname', productname);
        formData.append('category', category);
        formData.append('image', image);
        formData.append('document', 'la Ficha TÃ©cnica');
      } else {
        formData.append('productname', productname);
        formData.append('category', category);
        formData.append('image', image);
        formData.append('document', 'InformaciÃ³n de Seguridad');
      }
      formData.append('iduser', this.usersesion.ID);
      formData.append('dni', this.usersesion.document);
      this.bicinsaService.sendMail(formData)
        .subscribe((data) => {
          this.preloadingstate = false;
          const Toast = Swal.mixin({
            toast: true,
            position: 'top',
            showConfirmButton: false,
            timer: 3000
          });
          Toast.fire({
            type: 'success',
            title: 'Se enviarÃ¡ el documento mediante email.'
          })
          this.ficha = '';
          this.usuario = '';
          this.ngOnInit();
        },
          err => console.log(err),
        );
    } else {
      this.router.navigate(["/authenticate/login"]);
    }
  }

  alertConfirm() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });

    Toast.fire({
      type: 'success',
      title: 'Producto agregado exitosamente'
    })
  }

  alertNostock() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });

    Toast.fire({
      type: 'warning',
      title: 'Ya tienes todo el stock agregado'
    })
  }

  alertNoPresentation() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });

    Toast.fire({
      type: 'warning',
      title: 'Debes seleccionar una presentaciÃ³n'
    })
  }

  alertnumbervalid() {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top',
      showConfirmButton: false,
      timer: 3000
    });

    Toast.fire({
      type: 'warning',
      title: 'Ingresa una cantidad vÃ¡lida'
    })
  }

}
