import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Subject, Subscriber } from 'rxjs';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import { Product } from './shared/product';


//private API: string = 'https://www.bicinsa.com/API/api';
//const API: string = 'http://localhost:8000/api';
const API: string = 'http://binario.pe/server/bicinsa/api';

@Injectable({
  providedIn: 'root'
})

export class BicinsaService {
  private currentUserNameStore = new BehaviorSubject<string>("");
  public currentUserName$ = this.currentUserNameStore.asObservable();
  private itemsInCartSubject: BehaviorSubject<Product[]> = new BehaviorSubject([]);
  private itemsInCart: Product[] = [];

  private showconfirmemail = new BehaviorSubject<boolean>(true);
  public showconfirmemail$ = this.showconfirmemail.asObservable();

  private currentShowStore = new BehaviorSubject<boolean>(true);
  public currentShowStore$ = this.currentShowStore.asObservable();

  constructor(private http: HttpClient) {
    this.itemsInCartSubject.subscribe(_ => this.itemsInCart = _);
  }

  setCurrentUserName(userName: string) {
    this.currentUserNameStore.next(userName);
  }

  setshowconfirmemail(userName: boolean) {
    this.showconfirmemail.next(userName);
  }

  setCurrentShowStore(userName: boolean) {
    this.currentShowStore.next(userName);
  }

  listCategory(): any {
    return this.http.get(API + '/category');
  }

  listProductGeneral(): any {
    return this.http.get(API + '/products');
  }

  getSearchProduct(productsearch): any {
    return this.http.get(API + '/search/' + productsearch);
  }

  saveCliente(formData: Object) {
    return this.http.post(API + '/register', formData);
  }

  loginCliente(formData: Object): any {
    return this.http.post(API + '/login', formData);
  }

  getListProduct(idcategory): any {
    return this.http.get(API + '/listproduct/' + idcategory);
  }

  getListSubProduct(idcategory, idsubcategory): any {
    return this.http.get(API + '/listproduct/' + idcategory + '/subcategory/' + idsubcategory);
  }

  getDetailProduct(idproduct): any {
    return this.http.get(API + '/product/' + idproduct);
  }

  //AGREGAR PRODUCTOS AL CARRITO CUANDO ES VISITANTE (USUARIO SIN AUTENTICACION)
  addToCart(item) {
    let existe = false;
    let limitestock = false;
    //item.pricetotal = (parseFloat((item.price).toString()) * item.quantity).toFixed(2);
    var localstoragedata = JSON.parse(localStorage.getItem("shopping-list"));
    if (localstoragedata == null) {
      this.itemsInCartSubject.next([...JSON.parse(localStorage.getItem('shopping-list')) || [], item]);
      localStorage.setItem('shopping-list', JSON.stringify(this.itemsInCart));
    } else {
      for (let i = 0; i < localstoragedata.length; i++) {
        if (localstoragedata[i].ID == item.ID) {
          if (localstoragedata[i].presentation.ID == item.presentation.ID) {
            var long = localstoragedata[i].quantity;
            if (long.length > 3) {
              var num = localstoragedata[i].quantity.replace(',', '');
              localstoragedata[i].quantity = num;
            }
            localstoragedata[i].quantity = parseInt(localstoragedata[i].quantity) + parseInt(item.quantity);
            if (localstoragedata[i].quantity <= localstoragedata[i].stock) {
              //localstoragedata[i].pricetotal = (parseFloat((item.price).toString()) * parseInt(localstoragedata[i].quantity)).toFixed(2);
              limitestock = false;
            }
            else {
              localstoragedata[i].quantity = localstoragedata[i].stock;
              //localstoragedata[i].pricetotal = (parseFloat((item.price).toString()) * parseInt(localstoragedata[i].quantity)).toFixed(2);
              limitestock = true;
            }
            var num = localstoragedata[i].quantity.toString().replace(/\,/g, '');
            num = num.toString().split('').reverse().join('').replace(/(?=\d*\.?)(\d{3})/g, '$1,');
            num = num.split('').reverse().join('').replace(/^[\,]/, '');
            localstoragedata[i].quantity = num;
            existe = true;
          }
        }
      }
      if (existe) {
        localStorage.setItem('shopping-list', JSON.stringify(localstoragedata));
        this.itemsInCartSubject.next([...JSON.parse(localStorage.getItem('shopping-list')) || []]);
      } else {
        this.itemsInCartSubject.next([...JSON.parse(localStorage.getItem('shopping-list')) || [], item]);
        localStorage.setItem('shopping-list', JSON.stringify(this.itemsInCart));
      }
    }
    return limitestock;
  }


  getItems(): Observable<Product[]> {
    let usersesion = JSON.parse(localStorage.getItem("user"));
    if (usersesion != null) {
      this.getOrderUserLogin(usersesion.ID).map((data) => {
        if (data.length > 0) {
          this.setCurrentShowStore(true);
        } else {
          this.setCurrentShowStore(false);
        }
        this.itemsInCartSubject.next([...data]);
      }).toPromise();
      return this.itemsInCartSubject.asObservable();

    } else {
      this.itemsInCartSubject.next([...JSON.parse(localStorage.getItem('shopping-list')) || []]);
      return this.itemsInCartSubject.asObservable();
    }
  }
  removeFromCart(item: Product) {
    debugger
    var localstoragedata = JSON.parse(localStorage.getItem("shopping-list"));
    const itemsWithoutRemoved = localstoragedata.filter((_ => _.ID !== item.ID) && (_ => _.presentation.ID !== item.presentation.ID)); 
    localStorage.setItem('shopping-list', JSON.stringify(itemsWithoutRemoved));
    this.itemsInCartSubject.next([...JSON.parse(localStorage.getItem('shopping-list')) || []]);
  }

  verificarSession() {
    let usersesion = JSON.parse(localStorage.getItem("user"));
    var localstoragedata = JSON.parse(localStorage.getItem("shopping-list"));
    if (usersesion != null) {
      let formData = new FormData();
      for (let i = 0; i < localstoragedata.length; i++) {
        formData.append("productid[" + i + "]", localstoragedata[i].ID);
        formData.append("presentationid[" + i + "]", localstoragedata[i].presentation.ID);
        formData.append("quantity[" + i + "]", (localstoragedata[i].quantity).toString());
      }
      formData.append('idclient', usersesion.ID);
      this.saveOrder(formData)
        .subscribe((data) => {
        });
    }
  }

  getOrderUserLogin(idclient): any {
    return this.http.get(API + '/ordersxuser/' + idclient);
  }
  deleteFromOrder(formData: Object) {
    return this.http.post(API + '/deleteorder', formData);
  }
  saveOrder(formData: Object) {
    return this.http.post(API + '/saveorder', formData);
  }
  updateOrder(formData: Object) {
    return this.http.post(API + '/updateOrder', formData);
  }
  sendMail(formData: Object) {
    return this.http.post(API + '/documentmail', formData);
  }
  getOrders(idclient): any {
    return this.http.get(API + '/historyordersxuser/' + idclient);
  }
  getInfoUser(iduser): any {
    return this.http.get(API + '/infouser/' + iduser);
  }
  updateInfoUser(formData: Object) {
    return this.http.post(API + '/updateuser', formData);
  }
  forgotPassword(formData: Object): any {
    return this.http.post(API + '/forgotpassword', formData);
  }
  confirmTokenUser(formData: Object): any {
    return this.http.post(API + '/confirmtokenuser', formData);
  }
  resetPassword(formData: Object): any {
    return this.http.post(API + '/resetpassword', formData);
  }
  confirmTokenNewUser(formData: Object): any {
    return this.http.post(API + '/confirmtokennewuser', formData);
  }
  sendContact(formData: Object) {
    return this.http.post(API + '/contact', formData);
  }
}
